<template>
	<modal-wrap modalClass='modal-autodialogs' @close="close">
		<form class="form-dialog-settings" action="#!">
			<div class="tab-message">
				<div class="tabs-row">
					<v-button v-for="(tab, index) in messageTemplates"
					          :key="index"
					          @click="selection.selectedTab = index"
					          btn-style="empty"
					          :class="{'is-active': selection.selectedTab === index}"
					>
						{{ tab.id === 1 ? "Женский" : "Мужской"}}
					</v-button>
				</div>
				<button-wave class="tab-btn__save" type="submit">Сохранить</button-wave>
				<div class="tab-container">
						<transition name="slide-appearance" mode="out-in" appear>
							<div class="tab-message__container"
							     v-for="(tab, index) of messageTemplates"
							     v-if="selection.selectedTab === index"
							     :key="index"
							>
								<div class="tab-message__group">
									<button
										v-for="(button, index) of tab.template"
										class="v-btn v-button v-btn-empty btn-tab"
										@click="selection.selectedTemplate = index"
										:class="{'is-active': selection.selectedTemplate === index}"
										:key="index"
									>
										<span class="btn-tab__num">{{button.id}}</span>
										 <span class="btn-tab__text">сообщение</span>
									</button>
								</div>
								<transition name="slide-appearance" mode="out-in" appear>
									<div v-if="selection.selectedTemplate === index"
									     class="tab-message__content"
									     v-for="(template,index) of tab.template"
									     :key="index"
									>
										<transition-group name="slide-appearance" tag="div"
										                  class="tab-container-scrollable"
										                  v-if="template.messages.length"
										>
											<div class="tab-message__item"
											     v-for="(message,idx) of template.messages"
											     :key="message.id"
											     :id="message.id"
											>
												<v-text-field class="input-wrap"
												              label="Условие"
												              label-class="tab-message__label"
												              placeholder="{a|b|c}"
												              id="for-w"
												              v-model="message.condition"

												/>

												<v-text-area class="input-wrap"
												             placeholder="Введите что нибудь интересное"
												             label="Текст сообщения"
												             label-class="tab-message__label"
												             type="textarea"
												             id="for-textareaa"
												             v-model="message.text"
												/>
												<v-button class="tab-btn__change mt-20"
												          btn-style="empty"
												          @click="removeMessage(idx)"
												>Удалить
												</v-button>
											</div>
										</transition-group>
										<div v-else>add content</div>
									</div>
								</transition>
							</div>
						</transition>
					</div>
				<v-button class="tab-btn__add" @click="addTemplateMessage(selection)">+</v-button>
			</div>
		</form>
	</modal-wrap>
</template>

<script>
  import ModalWrap from './ModalWrap'
  import VButton from '../../molecules/buttons/VButton'
  import ButtonWave from '../../molecules/buttons/ButtonWave'
  import VTextField from '../../molecules/inputs/VTextField'
  import VTextArea from '../../molecules/inputs/VTextArea'
  import {mapActions, mapState} from 'vuex'

  export default {
    components: {
      ModalWrap,
      VButton,
      ButtonWave,
      VTextField,
      VTextArea,
    },
    data() {
      return {
        selection: {
          selectedTab: 0,
          selectedTemplate: 0,
          index: 0,
          // selectedTemplate: 0,
        }
      }
    },
    computed: {
      ...mapState(['messageTemplates']),
    },
    methods: {
      ...mapActions(['addTemplateMessage', 'removeTemplateMessage']),
      btnStyle(val) {
        if (this.selectedTemplate === val) return 'reg';
        else return 'empty'
      },
      close() {
        this.$emit('close');
      },
      removeMessage(index) {
        this.selection.index = index;
        console.log(index)
        this.removeTemplateMessage(this.selection);
      }
    }
  }
</script>
<style lang="scss">
	/*.form-dialog-settings {*/
		/*height: 100%;*/
	/*}*/

	.tabs-row {
		display: flex;
		margin-bottom: 30px;
		padding-bottom: 10px;
		border-bottom: 1px solid #4C5B5C;
		justify-content: space-between;
		.v-btn {
			width: 100%;
			margin-right: 10px;
			font-size: 14px;
			&:last-child {
				margin-right: 0;
			}
		}
	}

	.tab-message {
			position: relative;
			&__item {
				margin-bottom: 20px;
				&:last-child {
					margin-bottom: 0;
				}
				.v-btn {
					margin-left: auto;
					color: #fff;
				}
			}
			&__group {
				display: flex;
				justify-content: space-between;
				margin-bottom: 20px;
				.btn-tab {
					margin-right: 5px;
					width: 20%;
					&:last-child {
						margin-right: 0;
					}
					&__num {
						margin-right: 5px;
					}
					&__text {
						display: none;
					}
				}
			}
			&__content {
				max-width: 100%;
				width: 100%;
				max-height: 566px;
				overflow-x: hidden;
				overflow-y: auto;
			}
			&__label {
				/*font-size: 16px;*/
				/*color: #fff;*/
				margin-bottom: 4px;
			}

			.v-btn.is-active {
				background: #ffc107;
				color: #000;
			}
			.input-wrap {
				margin-bottom: 16px;
			}
			.waves-btn {
				.waves {
					transform: translate(-40px, 15px);
					width: 300px;
					height: 300px;
					&:hover {
						transform: translate(-140px, -20px)
					}
					&:after, &:before {
						pointer-events: none;
					}

				}
			}
	}
	.tab-container-scrollable {
		max-width: 440px;
		margin: 0 auto;
	}

	.tab-btn {
		&__save {
			/*position: absolute;*/
			margin-left: auto;
			margin-bottom: 20px;
			top: 0;
			right: 0;
		}
		&__add {
			position: absolute;
			left: 50%;
			transform: translate(-50%, 100%);
			bottom: 0;
			align-self: center;
			box-shadow: 0 0 9px 1px rgba(255, 193, 7, 0.8);
		}
	}

	@media screen and (min-width: 812px) {
		.tabs-row {
			border-bottom: 1px solid #4C5B5C;
			justify-content: flex-start;
			.v-btn {
				width: inherit;
				margin-right: 10px;
				font-size: 16px;
				color: #000;
				&:last-child {
					margin-right: 0;
				}
			}
		}
		.tab-message {
			.v-btn {
				color: #000;
			}
			.waves-btn {
				box-shadow: 0 8px 8px 2px rgba(#ffc107,.4);
				&__text {
					color: #000;
				}
			}
			.waves {
				&:after {
					background: #fff;
				}
				&:before {
					background: rgba(#fff,.6);
				}
			}
		}
		.tab-message__group {
			flex-direction: column;
			justify-content: flex-start;
			margin-right: 5px;
			.btn-tab {
				width: 100%;
				margin-right: 0;
				margin-bottom: 20px;
				&__text {
					display: inline-block;
				}
			}
		}
		.tab-btn__save {
			position: absolute;
			color: #000;
		}
		.tab-message__container {
			display: flex;
		}

	}

	.fade-group-enter-active, .fade-group-leave-active {
		transition: opacity .5s;
	}

	.fade-group-enter, .fade-group-leave-to /* .fade-leave-active до версии 2.1.8 */
	{
		opacity: 0;
	}

	.fade-left-enter-active, .fade-left-leave-to {
		opacity: 0;
		transition: .4s;
	}

	.fade-left-enter-to, .fade-left-leave-active {
		transform: translateX(100%);
	}

	.slide-fade-enter-active {
		transition: all .3s ease;
	}

	.slide-fade-leave-active {
		transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}

	.slide-fade-enter,
	.slide-fade-leave-to {
		transform: translateX(10px);
		opacity: 0;
	}


</style>